@import '../../../app/styles/variables/mixins';
@import '../../../app/styles/variables/variables';

.shopStorageModal_mainBlock {
  @include defaultModalStyle;
  @include flexOnlyBetween;
  gap: $defaultLargeMargin;
  min-width: 900px;

  .shopStorageModal_tree {
    background-color: $themeColorMilk;
    padding: $defaultLargeMargin;
    border-radius: $smallBorderRadius;
    width: 30%;
    color: black;

    display: flex;
    flex-direction: column;
    gap: 10px;

    .groups {
      flex-grow: 1;

      display: flex;
      justify-content: start;
      flex-direction: column;
      gap: 5px;

      .groupItem {
        display: flex;
        height: 40px;
        padding: 5px;
        align-items: center;
        justify-content: start;
        color: white;
        border-radius: 10px;
        background-color: #4b4b4b;
      }

      .groupItem:hover {
        background-color: #363636;
      }
    }

    .buttons {
      color: black;
      display: flex;
    }
  }

  @include createShopStorageModal(550px, 35%, 35%);
}